.button {
	border: 1px solid #6635fe;
	border-radius: 17px;
	box-shadow: 0px 0px 24px 0px rgba(102, 53, 254, 0.25);
	cursor: pointer;

	&-custom {
		background: conic-gradient(from 0.5turn at 50% 50%, #d0cbed, 220deg, #6635fe, 360deg, #6635fe);
		background: conic-gradient(from 0.5turn at 50% 50%, #f5f3ff, 220deg, #6635fe, 360deg, #6635fe);
		border-radius: 16px;
		padding: 1px;
	}

	&-text {
		font-size: 16px;
		font-weight: 700;
		font-style: normal;
		line-height: 150%;
		background-image: var(--button-text-color);
		background-size: 100%;
		background-clip: text;
		-moz-text-fill-color: transparent;
		-webkit-text-fill-color: transparent;
		font-style: normal;
		line-height: 150%;
	}
}

.border-custom {
	background: conic-gradient(from 0.5turn at 50% 50%, #d0cbed, 220deg, #6635fe, 360deg, #6635fe);
	background: conic-gradient(from 0.5turn at 50% 50%, #f5f3ff, 220deg, #6635fe, 360deg, #6635fe);
	border-radius: 16px;
	padding: 1px;
}

.content {
	display: flex;
	padding: 8px 26px;
	justify-content: center;
	align-items: center;
	gap: 10px;
	align-self: stretch;
	border-radius: 16px;
	background: linear-gradient(355deg, #5626eb 4.25%, #7c54f7 95.75%);
	box-shadow: 0px -4px 16px 0px rgba(94, 48, 232, 0.5) inset;
	border: 1px solid #C7B6FC;

	&:hover {
		border-radius: 16px;
		border: 1px solid #C7B6FC;
		background: linear-gradient(355deg, rgba(101, 69, 199, 1) 4.25%, rgba(124, 84, 247, 0.75) 95.75%);
		box-shadow: 0px -4px 16px 0px rgba(94, 48, 232, 0.50) inset;
	}
}

.greenPoint {
	background-color: #6bf754;
	width: 10px;
	height: 10px;
	border-radius: 50%;
	margin-right: 10px;
	box-shadow: 0 0 8px 0 rgba(101, 245, 84, 0.6), 0 0 16px 0 rgba(101, 245, 84, 0.3);
	margin-right: 0;
}
