.footer {
	&-container {
		background: var(--bg-main-color);
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		padding-top: 8rem;
		padding-bottom: 4rem;
		position: -webkit-sticky;
		position: sticky;
		bottom: 0;
		gap: 4rem;
		z-index: 0;
	}
	&-rights {
		display: flex;
		justify-content: center;
	}

	&-contactCard {
		display: flex;
		// width: 992px;
		position: relative;
		padding: 4rem;
		justify-content: space-between;
		align-items: center;
		border-radius: 2rem;
		background: linear-gradient(90deg, rgba(102, 53, 254, 0.07) 0%, rgba(220, 106, 57, 0.07) 100%);
		gap: calc(100vw / 25);
		margin: 0 calc(100vw / 16);

		&--info {
			display: flex;
			flex-direction: column;
			gap: 2rem;
		}
		&--logo {
			height: 369px;
			width: 379px;
		}
	}
	&-buttons {
		display: flex;
		gap: 1rem;
	}
	&-links {
		display: flex;
		flex-direction: column;
		width: fit-content;
		color: var(--text-secondary-color);
		font-family: Satoshi;
		font-size: var(--font-clamp-18-24);
		font-style: normal;
		font-weight: 500;
		line-height: 150%; /* 36px */
	}
	&-text {
		color: var(--text-secondary-color);
		font-size: var(--font-clamp-18-24);
		line-height: 1.5;
		font-weight: 500;
	}
	&-mail {
		color: var(--text-primary-color);
		font-size: var(--font-clamp-24-32);
		font-style: normal;
		font-weight: 500;
		line-height: 150%; /* 48px */
		text-decoration-line: underline;
	}
	&-copy {
		display: flex;
		padding: 8px 16px;
		align-items: center;
		gap: 4px;
		border-radius: 1rem;
		font-size: 1rem;
		font-weight: 700;
		line-height: 150%;
	}
	&-link {
		font-size: clamp(0.75rem, 5vw, 1.5rem);
		line-height: 1.5;
		font-weight: 500;
		color: var(--text-primary-color);
	}
	&-year {
		font-size: clamp(10px, 5vw, 12px);
		line-height: 1.5;
		font-weight: 400;
		color: var(--text-primary-color);
	}
}
.profile-link {
	font-size: 1rem;
	line-height: 1.5;
	font-weight: 700;
	color: #7c54f7;
}

.button-scroll-up {
	position: absolute;
	width: 3rem;
	height: 3rem;
	bottom: 1rem;
	right: 1rem;
	border-radius: 7.68px;
	border: 1px solid #9273f2;
	background: linear-gradient(355deg, #5626eb 4.25%, #7c54f7 95.75%);
	box-shadow: 0px 0px 64px 0px rgba(28, 28, 28, 0.12);
	cursor: pointer;
}

