/* * WORKS * */
.card-container {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	/* gap: 48px; */
}
.card-container > * {
	flex: 0 0 48%; /* This will make sure that only two items are in a row. Adjust the percentage as needed. */
	margin-bottom: 2%; /* Optional: add some vertical spacing between the rows */
}

/* * BOTH WORK * */
.work-card {
	display: flex;
	flex-direction: column;
	padding: 1rem;
	gap: 1rem;
}
.card{
	&-img {
		border-radius: 2rem;
		box-shadow: 0px 0px 64px 0px rgba(28, 28, 28, 0.12);
	}
	&-info {
		display: flex;
		flex-direction: column;
		gap: 0.5rem;
		&-title {
			color: var(--text-primary-color);
			font-size: var(--font-clamp-16-24);
			line-height: 1.5;
			font-weight: 700;
		}
		&-text {
			color: var(--text-secondary-color);
			font-size: var(--font-clamp-14-16);
			line-height: 1.5;
			font-weight: 400;
		}
	}
}


/* * BIG WORK * */

.work-big-card {
	width: 100%;
	height: clamp(422px, 50vw, 748px);
}

.big-img-h {
	height: clamp(320px, 40vw, 640px);
}

/* * SMALL WORK * */

.work-small-card {
	margin-top: 3rem;
	width: 100%;
	height: clamp(362px, 40vw, 628px);
}

.small-img-h {
	height: clamp(228px, 40vw, 488px);
}

/* * YOUR CARD  * */

.your-card{
	position: relative;
	display: flex;
	flex-direction: column;
	gap: 1rem;
	height: 100%;
	padding: 1.5rem 1.5rem 0 1.5rem;
    border-radius: 2rem;
    background-color: var(--your-card-bg);

	&-img{
		background: rgba(255, 255, 255, 0.36);
	}

	&-conatiner{
		position: relative;
		margin-top: 3rem;
		width: 100%;
		height: clamp(452px, 50vw, 734px);
		background: linear-gradient(to right, #6635FE 0%, #DC6A39 100%);
		padding: 1px;
		border: 1px solid transparent;
		border-radius: 2rem;
	}
}

.work-small-your-card{
	width: 100%;
	height: 100%;
}


