.card {
	width: calc(100vw - (100vw / 6));
	height: 100vh;
	display: flex;
	flex-direction: column;
	gap: 2rem;
	position: sticky;

	&-first {
		padding-top: 4rem;
		padding-bottom: 8rem;
		top: 2.5rem;
	}
	&-second {
		background: var(--color-bg-creme);
		border-radius: 6rem;
		padding: 8rem;
		width: 100vw;
        // z-index: 1;
	}
}

.card-content {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	gap: 4rem;
	align-self: stretch;

	&-heading {
		display: flex;
		flex-direction: column;
		align-items: center;
		gap: 1rem;

		&--text {
			position: relative;
			color: var(--text-primary-color);
			font-size: var(--font-clamp-24-48);
			line-height: 1.25;
			font-weight: 500;
			text-align: center;
			
		}
		&--text-dark{
			color: var(--color-text-black);
			position: relative;
			font-size: var(--font-clamp-24-48);
			line-height: 1.25;
			font-weight: 500;
			text-align: center;
		}
	}
}
.card-types {
	display: flex;
	justify-content: center;
	align-items: center;
	gap: 32px;
	align-self: stretch;
}
.card-type {
	display: flex;
	flex-direction: column;
	justify-content: center;
	padding: 0px 24px 24px 24px;
	gap: 24px;
	flex: 1 0 0;
	background: var(--Light-color-brand-accent-gradient, linear-gradient(1deg, #5626eb 0.92%, #7c54f7 99.08%));
	box-shadow: 0px 0px 64px 0px rgba(28, 28, 28, 0.12);
	border-radius: 2rem;

	&-img {
		width: 100%;
		height: 144px;
	}
	&-title {
		color: var(--color-text-white);
		font-size: var(--font-clamp-16-24);
		font-weight: 700;
		line-height: 150%;
	}
	&-text {
		color: var(--color-text-white);
		font-size: var(--font-clamp-14-16);
		font-weight: 400;
		line-height: 150%;
	}
}

.about-card {
	display: flex;
    gap: 3rem;;
}

/* * Profile Card */
.card-profile {
	display: flex;
	align-items: center;
	align-self: stretch;
	border-radius: 2rem;
	border: 1px solid rgba(28, 28, 28, 0.12);
	background: rgba(255, 255, 255, 0.24);
    overflow: hidden;

    &-content {
        display: flex;
        flex-direction: column;
        gap: 0.5rem;
        padding: 1.5rem;
    }

    &-title{
        font-size: var(--font-clamp-16-24);
        font-weight: 700;
        line-height: 150%;
    }
    &-text{
        font-size: var(--font-clamp-14-16);
        font-weight: 400;
        line-height: var(--line-height-clamp);
    }
}



.circled{
	&::after{
		content: '';
		position: absolute;
		width: 206px;
		height: 116px;
		top: -13px;
		left: 0;
		background-image: url('../../icons/circle.svg');
		margin: 0 auto;
		margin-top: 1rem;
		// z-index: 10;
	}
}