:root {
	--bg-main-color: #f1f1f1;
	--text-primary-color: #1c1c1c;
	--text-secondary-color: #363636;
	--badge-main: linear-gradient(90deg, rgba(102, 53, 254, 0.07) 0%, rgba(220, 106, 57, 0.07) 100%);
	--your-card-bg: rgba(255, 255, 255);

	/* Shadow 01 */
	box-shadow: 0px 0px 64px 0px rgba(28, 28, 28, 0.12);




	--color-brand-accent: #7c54f7;
	--button-text-color: linear-gradient(166deg, #fefefe -9.67%, #cac9cd 79.86%);

	/** Light Mode */
	--color-text-black: #1c1c1c;
	--color-text-gray: #363636;
	--color-background-light: #f1f1f1;
	--color-bg-creme: #ede8db;

	/** Dark Mode */
	--color-text-white: #f1f1f1;
	--color-background-dark: #191818;

	/** Font Clamp */
	--font-clamp-14-16: clamp(14px, 5vw, 16px);
	--font-clamp-16-24: clamp(16px, 5vw, 24px);
	--font-clamp-18-24: clamp(18px, 5vw, 24px);
	--font-clamp-24-32: clamp(24px, 5vw, 32px);
	--font-clamp-24-48: clamp(24px, 5vw, 48px);

	--line-height-clamp: clamp(1.25, 5vw, 1.5);
}

[data-theme='dark'] {
	--bg-main-color: #191818;
	--text-primary-color: #F1F1F1;
	--text-secondary-color: #E6E6E6;
	--badge-main: linear-gradient(90deg, rgba(102, 53, 254, 0.04) 0%, rgba(220, 106, 57, 0.04) 100%);
	--your-card-bg: rgb(0, 0, 0);

}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html {
	background-color: var(--color-background-light);
	/* background-color: green; */
}

body {
	margin: 0;
	font-family: 'Satoshi', sans-serif;
	/* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
		'Droid Sans', 'Helvetica Neue', sans-serif; */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.padding-scale {
	padding-left: calc(100vw / 12);
	padding-right: calc(100vw / 12);
}

.dark-text {
	color: var(--color-text-black);
}

.word-circled {
	background-image: url(/src/icons/circle.svg);
	overflow: visible;
	display: inline-block;
}

/* * NAVBAR */
.navbar {
	background-color: var(--color-background-light);
}

/* * TITLE TEXT */
.title-heading {
	font-size: clamp(2rem, 5vw, 4rem);
	font-style: normal;
	line-height: 1.5;
	font-weight: 700;
	text-transform: uppercase;
}

/* * BUTTOM */
.buton-shadow {
	width: 42px;
	height: 42px;
	border-radius: 50%;
	background-color: #6bf754;
	filter: drop-shadow(0 0 8px 0 rgba(0, 0, 0, 0.5), 0 0 16px 0 rgba(0, 0, 0, 0.5));
}

/* * OUR WORKS */

/* * HOW WE DO */
.bg-light {
	background-color: var(--color-background-light);
}

/* * SCROLLBAR * */
.scrollbar-hide::-webkit-scrollbar {
	display: none;
}
* ::-webkit-scrollbar {
	display: none;
}
/* For IE, Edge and Firefox */
.scrollbar-hide {
	/* -ms-overflow-style: none; IE and Edge */
	scrollbar-width: none; /* Firefox */
}

/* ! TESTING */
.test {
	background-color: #e8b5b560;
	border: 1px solid black;
}
.test-2 {
	background-color: #b5e8c356;
	border: 1px solid black;
}
.test-3 {
	background-color: #b5b8e856;
	border: 1px solid black;
}

@media (max-width: 1024px) {
	.word-circled {
		background-image: url(/src/icons/circle-sm.svg);
		overflow: visible;
		display: inline-block;
	}
}
