

.badge {
	display: flex;
	justify-content: center;
	align-items: center;
	width: fit-content;
	gap: 4px;
	padding: 8px;
	border-radius: 8px;
	background: var(--badge-main);
	
	&-text {
		font-size: clamp(12px, 5vw, 18px);
		font-weight: 700;
		font-style: normal;
		line-height: 120%;
		background: linear-gradient(90deg, #6635fe 0%, #dc6a39 100%);
		background-clip: text;
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
	}
}

